.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 300px;
  height: 100px;
  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
}

.subtotal-gift {
  display: flex;
  align-items: center;
}

.subtotal-gift > input {
  margin-right: 5px;
}

.subtotal > button {
  background-color: #f0c14b;
  width: 100%;
  height: 30px;
  border: 1px solid;
  border-radius: 2px;
  border-color: #a88734 #9c7e31;
  margin-top: 10px;
  color: black;
  cursor: pointer;
}

@media (max-width: 1000px) {
  .subtotal {
    width: 240px;
  }
}