.header {
	height: 60px;
	display: flex;
	align-items: center;
	top: 0;
	background-color: #131921;
	position: sticky;
	z-index: 100;
}

.header-logo {
	width: 100px;
	margin: 18px 20px 0 20px;
	object-fit: contain;
}

.header-search {
	display: flex;
	flex: 1;
	align-items: center;
}

.header-searchInput {
  width: 100%;
  padding: 8.1px;
  border: none;
}

.header-searchIcon {
	background-color: #cd9042;
	padding: 5px;
	height: 22px !important;
}

.header-nav {
  display: flex;
  justify-content: space-evenly;
}

.header-option {
  color: white;
  display: flex;
  flex-direction: column;
  margin: 0 10px 0 10px;
}

.header-optionOne {
  font-size: 10px;
}

.header-optionTwo {
  font-size: 13px;
  font-weight: 800;
}

.header-basketOption {
  color: white;
  display: flex;
  align-items: center;
}

.header-basketCount {
  margin: 0 10px;
}
